<template>
    <div>
        <div class="swiper">
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide"><img src="@/pictures/background/01.jpg" @load="preload(10)"></div>
                    <div class="swiper-slide"><img src="@/pictures/background/02.jpg" @load="preload(10)"></div>
                    <div class="swiper-slide"><img src="@/pictures/background/03.jpg" @load="preload(10)"></div>
                    <div class="swiper-slide"><img src="@/pictures/background/04.jpg" @load="preload(10)"></div>
                </div>
                <div class="swiper-button-prev"></div>
                <div class="swiper-button-next"></div>
            </div>
            <div class="swiper-pagination"></div>
        </div>
    </div>
</template>
<script>
import Swiper from 'swiper';
import "swiper/dist/css/swiper.min.css";
export default {
    name: "home",
    props:['ishome'],
    data() {
        return{
            loadding: 0,
        }
    },
    watch:{
        "ishome": function(){
            var _this = this;
            if(this.ishome === 4){
                _this.swiper_init();
            }
        }
    },
    mounted(){
        var _this = this;
        this.swiper_init();
        this.preload(60);
    },
    methods:{
        preload: function(e){
            var _this = this;
            this.loadding += e;
            if(this.loadding === 100){
                _this.$emit('homeLoadding', true);
            }
        },
        swiper_init: function(){
            var swiperContianer = new Swiper('.swiper-container', {
                    loop: true,        
                    direction: 'horizontal', //切换方向
                    effect: 'fade', //切换特效
                    observer: true,
                    observerParents: true,
                    observeSlideChildren:true,
                    pagination: {
                        el: '.swiper-pagination',
                        clickable :true,
                        renderBullet: function (index, className) {
			                return  '<span class="'+className+'"><img src="static/img/0' + (index + 1) + '.jpg"></span>';
		    	        },
                    },
                    navigation: {
                        nextEl: '.swiper-button-next',
                        prevEl: '.swiper-button-prev',
                    },
                    autoplay: true,
                    autoplay:{
                        disableOnInteraction: false,
                        delay: 2000,
                    },
                });
            swiperContianer.pagination.render();
            swiperContianer.autoplay.start();
        }
    }    
}
</script>
<style>
.swiper{
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -49%;
    margin-top: -40vh;
    width: 98%;
    height: 65vh;
    border-radius: 15px;
}

.swiper-container{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 50vh;
    border-radius: 15px;
}

.swiper-pagination{
    position: absolute;
    left: 50%;
    top: 51vh;
    margin-left: -30%;
    width: 60%;
    height: 14vh;
    border-radius: 15px;
}

.swiper-pagination-bullet{
	width:20% !important;
	height:auto !important;
    background:none !important;
}

.swiper-pagination-bullet img{
    width:100%;
}

.swiper-slide {
    border-radius: 15px;
    text-align: center;
    font-size: 18px;
    background: #fff;
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.swiper-slide img {
    max-width: 100%;
    height: auto;
} 
</style>



